.section__subtitle {
    font-family: var(--secondary-font-family);
    text-align: center;
    font-size: 18px;
    color: #ffa5ba;
    line-height: 1.4;
    margin-bottom: 15px;
}

.section__title {
font-family: var(--secondary-font-family);
text-align: center;
font-size: 34px;
color: #D41443;
margin-bottom: 30px;
}

.products__item-title {
    font-family: var(--secondary-font-family);
    text-align: center;
    color:  #FFFFFF;
    font-size: 30px;
    padding-bottom: 30px;
    margin-bottom: 30px;
    text-transform: uppercase;
    position: relative;
}

.products__item-description {
    margin-bottom: 60px;
    font-family: "Relaway" sans-serif;
    color: #FFFFFF;
    font-size: 14px;
    text-align: center;
}

.products__item-inner_pink {
    background-color: #FFB8CA;
}


.products__item-inner_coffee {
    background-color: #F0D1A5;
}

.products__item-inner_green {
    background-color: #C2E297;
}

.products__item-inner {
    padding: 120px 20px 30px 20px;
    border-radius: 10px;
}

